"use client";
import React, { useState } from "react";
import axios from "axios";
import { auth, googleProvider } from "../../config/firebaseConfig";
import { signInWithEmailAndPassword, signInWithPopup } from "firebase/auth";
import { useRouter } from "next/navigation";
import { Buffer } from "buffer";

export default function LoginPage() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const router = useRouter();

  const handleLogin = async (e) => {
    e.preventDefault();
    setError("");

    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password);
      const token = await userCredential.user.getIdToken();
      const encodedEmail = Buffer.from(email).toString("base64");
      const encodedPassword = Buffer.from(password).toString("base64");

      await axios.post("/api/login", {
        email: encodedEmail,
        password: encodedPassword,
        token: token,
      });

      alert("Logged in successfully!");
      router.push("/dashboard");
    } catch (err) {
      setError(err.message);
    }
  };

  return (
    <div style={styles.wrapper}>
      <div style={styles.card}>
        <h2 style={styles.title}>Login</h2>
        <p style={styles.subtitle}>Login to your account below</p>
        <form onSubmit={handleLogin}>
          <label>Email</label>
          <input
            type="email"
            placeholder="enter your email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            style={styles.input}
            required
          />
          <label>Password</label>
          <input
            type="password"
            placeholder="enter your password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            style={styles.input}
            required
          />
          <button type="submit" style={styles.button}>Login</button>
          {error && <p style={{ color: "red", marginTop: "10px" }}>{error}</p>}
        </form>
        <p style={styles.footer}>
          Don't have an account? <a href="/signup">Create New One</a>
        </p>
      </div>
    </div>
  );
}

const styles = {
  wrapper: {
    display: "flex", justifyContent: "center", alignItems: "center",
    height: "100vh", background: "#f5f5f5",
  },
  card: {
    background: "white", padding: "40px", borderRadius: "10px",
    boxShadow: "0 2px 8px rgba(0,0,0,0.1)", width: "350px"
  },
  title: { fontSize: "24px", fontWeight: "bold", textAlign: "center" },
  subtitle: { textAlign: "center", marginBottom: "20px", color: "#666" },
  input: {
    width: "100%", padding: "10px", marginBottom: "15px",
    borderRadius: "6px", border: "1px solid #ccc"
  },
  button: {
    width: "100%", padding: "10px", background: "black", color: "white",
    border: "none", borderRadius: "6px", cursor: "pointer"
  },
  footer: { textAlign: "center", marginTop: "10px", fontSize: "14px" }
};